<template>
  <nav>
    <div class="nav-wrapper green">
      <div class="container">
        <router-link to="/" class="brand-logo">werkHub</router-link>
        <ul class="right">
          <li v-if="isLoggedIn">
            <span class="email white-text">{{currentUser}}</span>
          </li>
          <li v-if="isLoggedIn">
            <router-link to="/">Dashboard</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/login">Login</router-link>
          </li>
          <li v-if="!isLoggedIn">
            <router-link to="/register">Register</router-link>
          </li>
          <li v-if="isLoggedIn">
            <button class="btn black" @click="logout">Logout</button>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
  import firebase from 'firebase'
  export default {
    name: 'navbar',
    data() {
      return {
        isLoggedIn: false,
        currentUser: false
      }
    },
    created() {
      let thisObj = this
      //Check if user logged in:
      if(firebase.auth().currentUser) {
        thisObj.isLoggedIn = true
        thisObj.currentUser = firebase.auth().currentUser.email
      }
    },
    methods: {
      logout() {
        let thisObj = this
        firebase.auth().signOut()
          .then(()=>{
            thisObj.$router.go({path: thisObj.$router.path})
          })
      }
    }
  }

</script>

<style scoped>
  .email {
    padding-right: 1em;
  }
</style>



