<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col s12 m8 offset-m2">
          <div class="login card-panel grey lighten-4 black-text center">
            <h3>Register</h3>
            <form>
              <div class="input-field">
                <i class="material-icons prefix">email</i>
                <input type="text" id="email" v-model="email">
                <label for="email">Email</label>
              </div>
              <div class="input-field">
                <i class="material-icons prefix">lock</i>
                <input type="text" id="password" v-model="password">
                <label for="password">Password</label>
              </div>
              <button class="btn btn-large grey lighten-4 black-text" @click="register">Register</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  export default {
    name: 'register',
    data: function(){
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      register(e) {
        e.preventDefault()
        let thisObj = this
        firebase.auth().createUserWithEmailAndPassword(thisObj.email, thisObj.password)
          .then(user => {
            alert(`Account created for ${user.user.email}`)
            thisObj.$router.go({path: thisObj.$router.path})
          }, err => {
            console.log(err.message)
          })

      },

    }
  }
</script>



