<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col s12 m8 offset-m2">
          <div class="login card-panel green white-text center">
            <h3>Login</h3>
            <form>
              <div class="input-field">
                <i class="material-icons prefix">email</i>
                <input v-model="email" type="text" id="email">
                <label class="white-text" for="email">Email</label>
              </div>
              <div class="input-field">
                <i class="material-icons prefix">lock</i>
                <input v-model="password" type="text" id="password">
                <label class="white-text" for="password">Password</label>
              </div>
              <button @click="login" class="btn btn-large grey lighten-4 black-text">Login</button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  export default {
    name: 'login',
    data: function(){
      return {
        email: '',
        password: ''
      }
    },
    methods: {
      login(e) {
        e.preventDefault()
        let thisObj = this
        firebase.auth().signInWithEmailAndPassword(thisObj.email, thisObj.password)
          .then(user => {
            alert(`You are logged in as ${user.user.email}`)
            thisObj.$router.go({path: thisObj.$router.path})
          }, err => {
            console.log(err.message)
          })

      },

    }
  }
</script>



